/* montserrat-300 - latin */
/* https://google-webfonts-helper.herokuapp.com/ */
@font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 300;
    src: url('montserrat-v18-latin/montserrat-v18-latin-300.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('montserrat-v18-latin/montserrat-v18-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('montserrat-v18-latin/montserrat-v18-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
         url('montserrat-v18-latin/montserrat-v18-latin-300.woff') format('woff'), /* Modern Browsers */
         url('montserrat-v18-latin/montserrat-v18-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
         url('montserrat-v18-latin/montserrat-v18-latin-300.svg#Montserrat') format('svg'); /* Legacy iOS */
  }
